import Vue from "vue";
import Router from "vue-router";

const routes = [
  {
    name: "about",
    path: "/about",
    component: () => import("@/about"),
  },
  {
    name: "notes",
    path: "/notes",
    component: () => import("@/notes"),
  },
  {
    name: "interesting",
    path: "/interesting",
    component: () => import("@/interesting"),
  },
  {
    name: "share",
    path: "/share",
    component: () => import("@/share"),
  },
  {
    name: "enter",
    path: "/enter",
    component: () => import("@/enter"),
  },
  {
    name: "markdown",
    path: "/markdown",
    component: () => import("@/markdown"),
  },
  {
    path: "/",
    component: () => import("@/enter"),
  },
  {
    name: "404",
    path: "/404",
    component: () => import("@/nothing"),
  },
  {
    name: "403",
    path: "/403",
    component: () => import("@/nothing"),
  },
  {
    path: "*",
    component: () => import("@/nothing"),
  },
];

Vue.use(Router);

export default new Router({ routes });
